.scroll.is-active {
  opacity: 1;
}

.scroll {
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  right: 6rem;
  bottom: 3.6rem;
  position: fixed;
  right: 6rem;
  bottom: 3.6rem;
  opacity: 0;
  z-index: 3;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  grid-gap: 1rem;
  gap: 1rem;
  pointer-events: none;
  transition: 0.4s ease;
}

.scroll .fill {
  height: 5rem;
  width: 0.1rem;
  position: relative;
  overflow: hidden;
  -webkit-transform: translateX(50%);
  transform: translateX(50%);
  width: 0.1rem;
  height: 5rem;
  -webkit-animation: scrollDown 2s ease infinite;
          animation: scrollDown 2s ease infinite;
}

.scroll .fill:after {
  content: "";
  left: 0;
  top: 0;
  z-index: -2;
  position: absolute;
  border-width: 0;
  border-left: 0.1rem solid rgba(255, 255, 255, 0.2);
  width: 0;
  height: 100%;
}

.scroll .fill:after {
  border-left-width: 0.1rem;
  border-color: rgb(255, 255, 255);
  border-style: solid;
}

.scroll .text {
  position: relative;
  font-size: 0.8rem;
  padding-top: 100%;
  -webkit-transform: translateX(50%);
  transform: translateX(50%);
}

.scroll .tag {
  color: #fff;
  letter-spacing: 3px;
  -webkit-transform: rotate(90deg) translateX(-50%);
  transform: rotate(90deg) translateX(-50%);
  -webkit-transform-origin: center center;
  transform-origin: center center;
}

@-webkit-keyframes scrollDown {
  0% {
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }
  45% {
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
  55% {
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
  100% {
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }
}

@keyframes scrollDown {
  0% {
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }
  45% {
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
  55% {
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
  100% {
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }
}

